<template>
    <div id="dashboard">
        <h3>Dashboard</h3>
        <ul class="collection with-header">
            <li class="collection-header"><h4>Employees</h4></li>
            <li class="collection-item" v-for="employee in employees" :key="employee.id">
                <div class="chip">{{ employee.dept }}</div>{{ employee.employee_id }} {{ employee.name }}
                <router-link :to="{name: 'view-employee', params: {employee_id: employee.employee_id}}"><i class="fa fa-eye"></i></router-link>
            </li>
        </ul>
        <div class="fixed-action-btn">
            <router-link to="/new" class="btn-float btn-large red">
                <i class="fa fa-plus"></i>
            </router-link>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'

export default {
    name: 'dashboard',
    data() {
        return {
            employees: []
        }
    },
    //run when component created
    created() {
        //get data
        db.collection('employees').orderBy('employee_id').get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                console.log(doc.data().name);
                const data = {
                    'id': doc.id,
                    'employee_id': doc.data().employee_id,
                    'name': doc.data().name,
                    'dept': doc.data().dept,
                    'position': doc.data().position,
                }
                this.employees.push(data);
            })
        })
    }
}
</script>


